.icon-title{
  margin: auto;
  display: block;
  width: 90pt;
  height: 90pt;
  line-height: 90pt!important;
  text-align: center;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  border-radius: 50%;
  border: 1pt dashed #ccc;
  font-size: 40pt;
  display: block;
}

.icon-title-small{
  margin: auto;
  display: block;
  width: 45pt;
  height: 45pt;
  line-height: 45pt!important;
  text-align: center;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  border-radius: 50%;
  border: 1pt dashed #ccc;
  font-size: 20pt;
  display: block;
}




.icon-image{
  color: @brand-white;
  transition: 0.5s;
  a{
    color: @brand-white;
    transition: 0.5s;
    &:hover{
      color: @brand-dark;
      background: @brand-light;
    }
  }
}



@font-face {
  font-family: 'icomoon';
  src:  url('/dist/fonts/icomoon.eot?fifbxu');
  src:  url('/dist/fonts/icomoon.eot?fifbxu#iefix') format('embedded-opentype'),
  url('/dist/fonts/icomoon.ttf?fifbxu') format('truetype'),
  url('/dist/fonts/icomoon.woff?fifbxu') format('woff'),
  url('/dist/fonts/icomoon.svg?fifbxu#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
}

[class^="icon-lip-"], [class*=" icon-lip-"] {
